.contactpage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.contacttitle {
  margin: 1rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.contacttitledesc {
  margin: 0.5rem;
  font-size: 1rem;
  font-weight: 100;
}

.contactform {
  margin: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100vw;
  max-width: 85%;
}

input,
textarea {
  font-size: medium;
  width: 100%;
  max-width: 40rem;
  margin: 0.5rem;
  padding: 0.5rem 1rem;
  color: rgb(8, 8, 8);
  border: none;
  background: rgb(205, 205, 208);
  border-radius: 0.5rem;
}

.formbtn {
  margin: 1rem;
  color: rgb(248, 250, 251);
  background: rgb(52, 164, 234);
  padding: 0.7rem 1rem;
  border: 1px solid white;
  border-radius: 2rem;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 300;
}
