.title {
  text-align: center;
  padding-top: 1rem;
}

.product {
  background-color: rgb(56, 171, 228);
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 2rem;
  margin: 0 2rem;
}

.card {
  display: flex;
  flex-direction: column;
  width: fit-content;
  margin: 1.5rem;
  /* border: 2px solid black; */
  border-radius: 10px;
  padding: 1rem;
}

.cardimg img {
  width: 18rem;
  height: 13rem;
  margin-bottom: 1rem;
}
