* {
  margin: 0;
  padding: 0;
}

.navbar {
  position: sticky;
  z-index: 3;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.linkitems {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none;
}

li {
  margin: 0 0.5rem;
  font-size: 1.2rem;
  padding: 0.3rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.logo img {
  height: 4rem;
  margin: 0.3rem;
}

li a.active {
  /* font-weight: bold; */
  color: rgb(37, 115, 199);
}
