* {
  margin: 0;
  padding: 0;
}

.homepage {
  background-image: url(../../assets/pexels-asadphoto-1268871.jpg);
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  align-items: center;
}

.welcometext {
  color: black;
  font-size: 1rem;
  font-weight: 200;
  margin-top: 0.5rem;
}

.welcometext1 {
  padding: 0.5rem;
  font-size: 2rem;
}

.button {
  text-decoration: none;
  color: white;
  background-color: rgb(33, 32, 32);
  width: fit-content;
  border-radius: 1rem;
  padding: 0.5rem;
  margin: 2rem;
  cursor: pointer;
}
